/**
 * Base styles
 */

html, body, #root {
  // mobile viewport bug fix (safari)
  height: -webkit-fill-available;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  width: 100%;
  margin: 0;
  font-family: $body-font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
  color: $font-colour;
}

.app {
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
  // mobile viewport bug fix (safari)
  min-height: -webkit-fill-available;
}

.page {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0; // needed for home page to centre
  right: 0; // needed for home page to centre

  a {
    position: relative;
  }
}




/**
 * Loader in About.js
 * Mark - do we need this?
 */
.smooth-image {
  /* transition: opacity 1s; */
  transition: opacity 0.5s ease-in;
}
.image-visible {
  opacity: 1;
}
.image-hidden {
  opacity: 0;
  transition-delay: 5s;
}